function BtnEventDel() {
var str = document.getElementById('calc').innerHTML;
var arr = [' + ', ' - ', ' * ', ' / '];
for(var i = 0; arr.length > i; i++) {
    if(str.slice(-3) === arr[i]) {
        return (document.getElementById('calc').innerHTML = document.getElementById('calc').innerHTML.slice(0, -3),
                document.getElementById('show').innerHTML = document.getElementById('show').innerHTML.slice(0, -3)
        );
    };
};
    return (
        document.getElementById('show').innerHTML = document.getElementById('show').innerHTML.slice(0, -1),
        document.getElementById('calc').innerHTML = document.getElementById('calc').innerHTML.slice(0, -1),
        document.getElementById('result').innerHTML = '',
        document.getElementById('test').innerHTML = document.getElementById('test').innerHTML.slice(0, -1)
    );
};

export default BtnEventDel;