function LimitInseration() {
    var str = document.getElementById('calc').innerHTML;
    var openBracket = 0;
    var closeBracket = 0;
    var arr = ['(', '+', '-', '*', '/'];
    // block insert ')';
    for (var x = 0; arr.length > x; x++) {
        if(str.slice(-1) === arr[x]) {
            return false;
        };
    };

    // calculate the number of '(';
    for(var i of str) {
        if(i === '(') {
            ++openBracket;
        };
    };

    // calculate the number of ')';
    for(var j of str) {
        if(j === ')') {
            ++closeBracket;
        }; 
    };

    // 1. blocks insertion if str length is 0;
    // 2. blocks insertion if the last symbol is '(';
    // 3. blocks insertion if the number of symbols '(' and ')' are equal;
    // 4. insert symbol ')';
    if(str.length === 0) {
        return false;
    }else if(openBracket === closeBracket) {
        return false;
    }else return (document.getElementById('show').innerHTML += ')',
                  document.getElementById('calc').innerHTML += ')'
    );
};

function BtnEventEndBracket() {
    return (
        LimitInseration()
    );
};

export default BtnEventEndBracket;