import React from 'react';
import './btnTheme.css';

function DarkPosition() {
    var els = document.getElementsByClassName('btnOtherSymbol');
    var els1 = document.getElementsByClassName('btnOperator');
    var els2 = document.getElementsByClassName('textBtnOperator');
    var els3 = document.getElementsByClassName('btnNumber');
    var els4 = document.getElementsByClassName('textBtnNumber');

    for(var i = 0; els.length > i; i++) {
        els[i].style.backgroundColor = '#4d463c';
        els[i].style.boxShadow = '2px 2px 5px #2c271d, 2px 2px 2px #525252 inset';
    };
    for(var j = 0; els1.length > j; j++) {
        els1[j].style.backgroundColor = '#48335c';
        els1[j].style.boxShadow = '2px 2px 5px #1d2420, 2px 2px 2px #4d534e inset';
    };
    for(var k = 0; els2.length > k; k++) {
        els2[k].style.color = '#c785f7';
    };
    for(var l = 0; els3.length > l; l++) {
        els3[l].style.backgroundColor = '#45474a';
        els3[l].style.boxShadow = '2px 2px 5px #1d2327, 2px 2px 2px #55575a inset';
    };
    for(var m = 0; els4.length > m; m++) {
        els4[m].style.color = '#bfc0b9';
    };   

    if(window.innerWidth <= 420) {
        document.getElementById('position').style.marginLeft = '7.2vw';
    }else if(window.innerWidth <= 992) {
        document.getElementById('position').style.marginLeft = '3vw';
    }else if(window.innerWidth >= 993) {
        document.getElementById('position').style.marginLeft = '30px';
    }

    return (
        document.getElementById('root').style.backgroundImage = 'linear-gradient(#2d3235, #1e2225)',
        document.getElementById('mainCase').style.backgroundImage = 'linear-gradient(#676b6c, #202325)',
        document.getElementById('mainCase').style.boxShadow = '10px 10px 20px #131618, 0 0 20px #2a2e2f',
        document.getElementById('btnC').style.backgroundColor = '#bb871f',
        document.getElementById('btnC').style.boxShadow = '2px 2px 5px #3e3517, 2px 2px 2px #ad957d inset',
        document.getElementById('btnDel').style.backgroundColor = '#2f3438',
        document.getElementById('btnDel').style.boxShadow = '2px 2px 5px #161618, 2px 2px 2px #3c4143 inset',
        document.getElementById('btnEqual').style.backgroundColor = '#8e27de',
        document.getElementById('btnEqual').style.boxShadow = '2px 2px 5px #1a152e, 2px 2px 2px #846e8f inset',
        document.getElementById('show').style.color = '#a8b1b0',
        document.getElementById('result').style.color = '#a8b1b0',
        document.getElementById('btnTheme').style.boxShadow = '2px 2px 5px #232421, 2px 2px 2px #6b6b6c inset',
        document.getElementById('btnTheme').style.backgroundColor = '#3e4044'
        
        
    );
};

function LightPosition() {
    var els = document.getElementsByClassName('btnOtherSymbol');
    var els1 = document.getElementsByClassName('btnOperator');
    var els2 = document.getElementsByClassName('textBtnOperator');
    var els3 = document.getElementsByClassName('btnNumber');
    var els4 = document.getElementsByClassName('textBtnNumber');
    for(var i = 0; els.length > i; i++) {
        els[i].style.backgroundColor = '#f6f0e4';
        els[i].style.boxShadow = '2px 2px 5px #e6d3ab, 2px 2px 2px #f3f7ea inset';
    };
    for(var j = 0; els1.length > j; j++) {
        els1[j].style.backgroundColor = '#e1d5e9';
        els1[j].style.boxShadow = '2px 2px 5px #ccafe9, 2px 2px 2px #f6ddfa inset';
    };
    for(var k = 0; els1.length > k; k++) {
        els2[k].style.color = '#470078';
    };
    for(var l = 0; els3.length > l; l++) {
        els3[l].style.backgroundColor = '#f4f5f7';
        els3[l].style.boxShadow = '2px 2px 5px #d7d9d8, 2px 2px 2px #fcf8f8 inset';
    };
    for(var m = 0; els4.length > m; m++) {
        els4[m].style.color = '#516975';
    };
    
    return (
        document.getElementById('position').style.marginLeft = 0 + 'px',
        document.getElementById('root').style.backgroundImage = 'linear-gradient(#e6e6e9, #dfdfdf)',
        document.getElementById('mainCase').style.backgroundImage = 'linear-gradient(#f6f6f8, #f1f1f1)',
        document.getElementById('mainCase').style.boxShadow = '10px 10px 20px #aeaeb1, 0 0 20px #e6e4df',
        document.getElementById('btnC').style.backgroundColor = '#fdb82c',
        document.getElementById('btnC').style.boxShadow = '2px 2px 5px #fcc352, 2px 2px 2px #f7d083 inset',
        document.getElementById('btnDel').style.backgroundColor = '#e4e8eb',
        document.getElementById('btnDel').style.boxShadow = '2px 2px 5px #ccd0d3, 2px 2px 2px #eff3e6 inset',
        document.getElementById('btnEqual').style.backgroundColor = '#c55cda',
        document.getElementById('btnEqual').style.boxShadow = '2px 2px 5px #9b83bf, 2px 2px 2px #e1a5f0 inset',
        document.getElementById('show').style.color = '#5e6871',
        document.getElementById('result').style.color = '#5e6871',
        document.getElementById('btnTheme').style.boxShadow = '2px 2px 5px #aaabb3, 2px 2px 2px #fff8e3 inset',
        document.getElementById('btnTheme').style.backgroundColor = '#969ea2'
    );
};

class Toggle extends React.Component {
    constructor(props) {
        super(props);
        this.state = {isToggleOn: true};
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn
        }));
        this.state.isToggleOn ? DarkPosition() : LightPosition();
    }

    render() {
        return (
            <div id='theme'>
            <div id='btnTheme' onClick={this.handleClick}>
                <div id='position'></div>
            </div>
            <p id='themeText'>
                {this.state.isToggleOn ? 'Dark theme' : 'Light theme'}
            </p>
        </div>
        )
    }
}

export default Toggle;