function LimitInseration() {
    var str = document.getElementById('calc').innerHTML;
    var arr = [' + ', ' - ', ' / '];
    var arr1 = ['', '('];
    document.getElementById('test').innerHTML = '';

    // if the minus symbol is the first, we delete it;
    if(str.length === 3 && str.slice(-3) === ' - ') {
        return (document.getElementById('show').innerHTML = document.getElementById('show').innerHTML.slice(0, -3),
                document.getElementById('calc').innerHTML = document.getElementById('calc').innerHTML.slice(0, -3)
        );
    };

    // if the minus symbol is the first in start bracket, we delete it;
    if(str.slice(-4) === '( - ') {
        return (document.getElementById('show').innerHTML = document.getElementById('show').innerHTML.slice(0, -3),
                document.getElementById('calc').innerHTML = document.getElementById('calc').innerHTML.slice(0, -3)
        );
    };

    // if change operator;
    for(var i = 0; arr.length > i; i++) {
        if(str.slice(-3) === arr[i]) {
            return (document.getElementById('show').innerHTML = document.getElementById('show').innerHTML.slice(0, -3) + ' * ',
                    document.getElementById('calc').innerHTML = document.getElementById('calc').innerHTML.slice(0, -3) + ' * '
            )
        };
    };

    // blocking the insertion of the multiplication symbol;
    for(var j = 0; arr1.length > j; j++) {
        if(str.slice(-1) === arr1[j]) {
            return false;
        };
        if(str.slice(-3) === ' * ') {
            return false;
        };
    };

    // insert multiplication symbol; 
    return (document.getElementById('show').innerHTML += ' * ',
            document.getElementById('calc').innerHTML += ' * '
    );
};

function BtnEventMultiplication() {
    return (
        LimitInseration()
    );
};

export default BtnEventMultiplication;