import Toggle from "./BTN/btnTheme";

function Display() {
    return (
        <div id='display'>
            <div id='showBox'><p id='show'></p></div>
            <p id='calc'></p>
            <div id='resultBox'><p id='result'></p></div>
            <p id='test'></p>
            <Toggle />
        </div>
    );
};

export default Display;