function BtnEventDot() {
    var str = document.getElementById('test').innerHTML;
    var str1 = document.getElementById('calc').innerHTML;
    var plus = 0;
    var arr = [];
    var arr1 = ['+', '-', '*', '/', '('];
    plus = plus + 1;

    // enter the number in arr;
    for(var k = 0; k < str.length; k++) {
        if(str[k] === '.') {
            arr.push(str[k]);
        };
    };

    // checking the length of the arr;
    if(arr.length === plus) {
        return false;
    };

    // if last symbol is end bracket;
    if(str1.slice(-1) === ')') {
        return (document.getElementById('show').innerHTML += '*0,',
                document.getElementById('calc').innerHTML += '*0.',
                document.getElementById('test').innerHTML += '*0.'
        );
    };

    // blocking the insertion of items in arr1;
    for(var x = 0; arr1.length > x; x++) {
        if(str.slice(-1) === arr1[x]) {
            return (document.getElementById('show').innerHTML += '0,',
                    document.getElementById('calc').innerHTML += '0.',
                    document.getElementById('test').innerHTML += '0.'
            );
        };
    };

    // if the length of the str is zero;
    if(str.length === 0) {
        return (document.getElementById('show').innerHTML += '0,',
                document.getElementById('calc').innerHTML += '0.',
                document.getElementById('test').innerHTML += '0.'
        );
    }else return (
        document.getElementById('show').innerHTML += ',',
        document.getElementById('test').innerHTML += '.',
        document.getElementById('calc').innerHTML += '.'
        );
};

export default BtnEventDot;