function Num4() {
    var str = document.getElementById('calc').innerHTML;
    
    // the number inserted after );
    if(str.slice(-1) === ')') {
        return (document.getElementById('show').innerHTML += '*4',
                document.getElementById('calc').innerHTML += '*4',
                document.getElementById('test').innerHTML += '*4'
        );
    }else if(str.length >= 200) {
        alert('You have entered too many symbols!');
        return false;
    }else {
        return (document.getElementById('show').innerHTML += document.getElementById('num4').innerHTML,
                document.getElementById('calc').innerHTML += document.getElementById('num4').innerHTML,
                document.getElementById('test').innerHTML += document.getElementById('num4').innerHTML
        );
    };
}

export default Num4;