import BtnC from './BTN/btnC';
import BtnDel from './BTN/btnDel';
import BtnOperator from './BTN/btnOperator'
import BtnOtherSymbol from './BTN/btnOtherSymbol';
import BtnEqual from './BTN/btnEqual';
import BtnNumber from './BTN/btnNumber';
import Num0 from './WrittenEvent/num0';
import Num1 from './WrittenEvent/num1';
import Num2 from './WrittenEvent/num2';
import Num3 from './WrittenEvent/num3';
import Num4 from './WrittenEvent/num4';
import Num5 from './WrittenEvent/num5';
import Num6 from './WrittenEvent/num6';
import Num7 from './WrittenEvent/num7';
import Num8 from './WrittenEvent/num8';
import Num9 from './WrittenEvent/num9';
import BtnEventC from './WrittenEvent/btnEventC';
import BtnEventDel from './WrittenEvent/btnEventDel';
import BtnEventPlus from './WrittenEvent/btnEventPlus';
import BtnEventMinus from './WrittenEvent/btnEventMinus';
import BtnEventDivision from './WrittenEvent/btnEventDivision';
import BtnEventMultiplication from './WrittenEvent/btnEventMultiplication';
import BtnEventEqual from './WrittenEvent/btnEventEqual';
import BtnEventStartBracket from './WrittenEvent/btnEventStartBracket';
import BtnEventEndBracket from './WrittenEvent/btnEventEndBracket';
import BtnEventDot from './WrittenEvent/btnEventDot';
import BtnEventRadical from './WrittenEvent/btnEventRadical';
import BtnEventPrecent from './WrittenEvent/btnEventPrecent';
import BtnEventPow from './WrittenEvent/btnEventPow';
import BtnHidden from './BTN/btnHidden';


function ButtonsCase() {
    return (
        <div id='buttonsCase'>
            <BtnC text={<p id='textBtnC' onClick={BtnEventC}>C</p>} />
            <BtnOtherSymbol text={<p id='textBtnOtherSymbol' onClick={BtnEventStartBracket}>(</p>} />
            <BtnOtherSymbol text={<p id='textBtnOtherSymbol' onClick={BtnEventEndBracket}>)</p>} />
            <BtnOperator text={<p className='textBtnOperator' onClick={BtnEventMultiplication}></p>} />
            <BtnOtherSymbol text={<p id='textBtnOtherSymbol' onClick={BtnEventRadical}></p>} />
            <BtnOtherSymbol text={<p id='textBtnOtherSymbol' onClick={BtnEventPrecent}>%</p>} />
            <BtnOtherSymbol text={<p id='sup' onClick={BtnEventPow}>x<sup>y</sup></p>} />
            <BtnOperator text={<p className='textBtnOperator' onClick={BtnEventDivision}></p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num7' onClick={Num7}>7</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num8' onClick={Num8}>8</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num9' onClick={Num9}>9</p>} />
            <BtnOperator text={<p className='textBtnOperator' onClick={BtnEventMinus}>-</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num4' onClick={Num4}>4</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num5' onClick={Num5}>5</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num6' onClick={Num6}>6</p>} />
            <BtnOperator text={<p className='textBtnOperator' onClick={BtnEventPlus}>+</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num1' onClick={Num1} value={1}>1</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num2' onClick={Num2}>2</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num3' onClick={Num3}>3</p>} />
            <BtnEqual text={<p id='textBtnEqual' onClick={BtnEventEqual}>=</p>} />
            <BtnHidden  text={<p className='textBtnNumber'>?</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='dot' onClick={BtnEventDot}>.</p>} />
            <BtnNumber text={<p className='textBtnNumber' id='num0' onClick={Num0}>0</p>} />
            <BtnDel text={<p id='textBtnDel' onClick={BtnEventDel}></p>} />
        </div>
    )
}

export default ButtonsCase;