function BtnEventPrecent() {
    var str = document.getElementById('test').innerHTML;
    var num = str.length;
    document.getElementById('calc').innerHTML = document.getElementById('calc').innerHTML.slice(0, -num);
    return (document.getElementById('show').innerHTML += '%',
            document.getElementById('calc').innerHTML += '(' + str + '*0.01)'
    );
};

export default BtnEventPrecent;