function Num3() {
    var str = document.getElementById('calc').innerHTML;
    
    // the number inserted after );
    if(str.slice(-1) === ')') {
        return (document.getElementById('show').innerHTML += '*3',
                document.getElementById('calc').innerHTML += '*3',
                document.getElementById('test').innerHTML += '*3'
        );
    }else {
        return (document.getElementById('show').innerHTML += document.getElementById('num3').innerHTML,
                document.getElementById('calc').innerHTML += document.getElementById('num3').innerHTML,
                document.getElementById('test').innerHTML += document.getElementById('num3').innerHTML
        );
    };
}

export default Num3;