function Num0() {
    var str = document.getElementById('calc').innerHTML;
    // the number inserted after );
    if(str.slice(-1) === ')') {
        return (document.getElementById('show').innerHTML += '*0',
                document.getElementById('calc').innerHTML += '*0',
                document.getElementById('test').innerHTML += '*0'
        );
    }else if(str.length >= 200) {
        alert('You have entered too many symbols!');
        return false;
    }else {
        return (document.getElementById('show').innerHTML += document.getElementById('num0').innerHTML,
                document.getElementById('calc').innerHTML += document.getElementById('num0').innerHTML,
                document.getElementById('test').innerHTML += document.getElementById('num0').innerHTML
        );
    };
}

export default Num0;