function LimitInseration() {
    var str = document.getElementById('calc').innerHTML;
    var arr = [' + ', ' - ', ' * ', ' / ', '('];
    document.getElementById('test').innerHTML = '';
    
    // insert the symbol '(' if in front of it is one of the symbols in arr;
    for (var i = 0; arr.length > i; i++) {
        if(str.slice(-3) === arr[i]) {
            return (document.getElementById('show').innerHTML += '(',
                    document.getElementById('calc').innerHTML += '('
            );
        };
    };

    // insert the '(' symbol if there is nothing in front of it;
    if(str.length === 0) {
        return (document.getElementById('show').innerHTML += '(',
                document.getElementById('calc').innerHTML += '('
        );
    }else return (document.getElementById('show').innerHTML += '*(',
                  document.getElementById('calc').innerHTML += '*('
    );
};

function BtnEventStartBracket() {
    return (
        LimitInseration()
    );
};

export default BtnEventStartBracket;